export default {
	data() {
		return {
			disabled: false,
			btnText: "获取验证码",
			mobile: "",
			type: "primary",
			code: ""
		}
	},
	methods: {
		
		//读秒循环
		doLoop: function(seconds, phone) {
			let that = this;
			let getCodeUrl = this.formatStr(this.$base_api.getPhoneCode, phone);
			console.log("获取的url{}", getCodeUrl);
			this.$minApi.uniapp_get(getCodeUrl, {}).then(res => {
				if (res.success) {
					that.code = res.data;
				}
			})
			seconds = seconds ? seconds : 60;
			this.btnText = seconds + "s后获取";
			let countdown = setInterval(() => {
				if (seconds > 0) {
					this.btnText = seconds + "s后获取"
						--seconds;
				} else {
					this.btnText = "获取验证码";
					this.disabled = false;
					this.type = "primary";
					clearInterval(countdown);
				}
			}, 1000);
		},
		
		//发送验证码
		btnSend: function() {
			if (this.isBlank(this.mobile)) {
				this.$msg('请输入手机号码');
				return
			} else if (!this.global.isMobilePhone(this.mobile)) {
				this.$msg('请输入正确的手机号码');
				return
			}
			this.disabled = true;
			this.btnText = "请稍候...";
			this.type = "gray"
			setTimeout(() => {
				this.doLoop(60, this.mobile)
			}, 500)
		},
		formLogin: function(e) {
			let code = e.detail.value.smsCode;
			let phone = e.detail.value.mobile;
			console.log("验证码{}", code);
			console.log("全局code{}",this.code);
			let that = this;
			if (this.isBlank(phone)) {
				this.$msg('请输入手机号码');
				return
			} else if (!this.global.isMobilePhone(phone)) {
				this.$msg('手机号格式不正确');
				return
			} else if (this.isBlank(code)) {
				this.$msg('请输入验证码');
				return
			} else if (code != this.code) {
				this.$msg('验证码不正确');
				return
			}
			let param = new Object();
			param.phone = phone;
			param.code = code;
			param.mallId = this.$base_api.MALL_ID;
			this.$minApi.uniapp_post(this.$base_api.phoneLogin, JSON.stringify(param), 'json').then(login_res => {
				if (login_res.success) {
					that.$msg("登陆成功");
					that.$cache.set(that.global.USER_PHONE, phone);
					//跳转到我的页面
					that.$openPage('user');
				} else {
					that.$msg("登录失败{}", login_res.msg);
				}
			});
		},
		protocol: function() {
			uni.navigateTo({
				url: '../about/about'
			})
		}
	}
}
